// I cannot get templates to work.


function createImageModal(image: HTMLImageElement): HTMLDialogElement
{
    const dialog = document.createElement("dialog") as HTMLDialogElement
    dialog.classList.add("image-modal")
    dialog.append(image)
    dialog.addEventListener("click", event =>
    {
        const bounds = dialog.getBoundingClientRect()
        if (
            event.clientX < bounds.left
            || event.clientX > bounds.right
            || event.clientY < bounds.top
            || event.clientY > bounds.bottom)
            dialog.close()
    })
    return dialog
}

function createProjectItem(vars: EnginePageConfigVars, itemInfo: EnginePageProjectConfig)
{
    const root = document.createElement("div") as HTMLDivElement
    root.classList.add("col-4", "col-12-mobile")

    const article = document.createElement("article") as HTMLElement
    article.classList.add("item")

    const aTag = document.createElement("a") as HTMLAnchorElement
    aTag.href = `${vars.gitHubPageURL}/${itemInfo.link.value}`

    const imgTag = document.createElement("img") as HTMLImageElement
    imgTag.title = "Click to enlarge"
    const dialog = createImageModal(imgTag)
    imgTag.src = `${vars.imageUrl}/${itemInfo.image.name}`
    const size = itemInfo.image.style.size

    imgTag.style.width = `${size.width}px`
    imgTag.style.height = `${size.height}px`
    imgTag.addEventListener("click", _ => dialog.showModal() )
    
    dialog.style.width = `${size.width}px`
    dialog.style.height = `${size.height}px`
    
    //imgTag.style.objectFit = itemInfo.image.style.fit ?? "cover"
    const header = document.createElement("header") as HTMLElement

    const h3Tag = document.createElement("h3") as HTMLHeadingElement
    h3Tag.textContent = itemInfo.name
    header.append(aTag)

    aTag.append(h3Tag)
    article.append(imgTag, dialog)
    root.append(article)

    return root


}


function setupEnginePage(config: EnginePageConfig)
{
    console.log(config)
    const {variables, pages} = config
    const vars = variables ?? {gitHubPageURL: "", imageUrl: ""}   satisfies EnginePageConfigVars
    const row = document.createElement("div") as HTMLDivElement
    row.classList.add("row")
    console.log(pages!)
    for (const page of pages!)
    {
        row.append(createProjectItem(vars, page))
    }
    console.log("Is container valid? ", container != null)
    container.append(row)
}


function onDOMContentLoaded()
{
    container = document.querySelector("section#portfolio div.container") as HTMLDivElement
    console.log("Is container valid? ", container != null)
    setupEnginePage(enginePagesConfig.Unity!)
}

document.addEventListener("DOMContentLoaded", onDOMContentLoaded)


